import Swiper from 'swiper';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Navigation, Pagination, Autoplay } from 'swiper/modules';

export function initSwiper() {
    new Swiper(".slider-achievement", {
        modules: [Autoplay], // B sung modules
        loop: true,
        autoplay: {
            delay: 0,
            disableOnInteraction: false,
        },
        speed: 5000,
        slidesPerView: 2,
        spaceBetween: 8,
        // Responsive theo tng kch thc mn hnh
        breakpoints: {
            480: {
                slidesPerView: 2, // in thoi nh
                spaceBetween: 8,
            },
            768: {
                slidesPerView: 4, // Tablet
                spaceBetween: 10,
            },
            1024: {
                slidesPerView: 8, // Laptop tiu chun
                spaceBetween: 12,
            },
            1366: {
                slidesPerView: 12, // Mn hnh ln hn
                spaceBetween: 15,
            },
            1600: {
                slidesPerView: 16, // Mn hnh rt ln
                spaceBetween: 18,
            },
            1920: {
                slidesPerView: 20, // Mn hnh cc ln (Full HD)
                spaceBetween: 20,
            },
        },
    });
}
